@charset "utf-8";
@import url("https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:ital,wght@0,200..800;1,200..800&display=swap");
@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.9/dist/web/static/pretendard.min.css");

.mainPage {
  margin: 0 auto;
  background-color: #101010;
  padding: 0px 0px 0px 100px;
  color: #fff;
}

.mainPage * {
  box-sizing: border-box;
}

.mainPage .mainContents {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  background-color: #101010;
}

.mainPage main {
  width: 100%;
  overflow: hidden;
}

.mainPage section {
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

#mainVisualWrap {
  align-items: center;
  width: 100%;
  height: 100vh;
  background-position: center 300px;
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  background-color: #040303;
  animation: opening 1.5s ease forwards;
}

@keyframes opening {
  100% {
    background-position: center;
  }
}

#mainVisualWrap .visualTitle {
  width: calc(100% - 100px);
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 0;
  /* transform: translateY(50px); */
  transition: 0.75s ease 1.2s;
}

#mainVisualWrap.is_current .visualTitle {
  /* transform: translateY(0); */
  opacity: 1;
}

#mainVisualWrap .visualTitle svg {
  display: block;
}

#mainVisualWrap .visualTitle svg:first-child {
  margin-right: auto;
  margin-bottom: 20px;
  stroke-dasharray: 500;
  stroke-dashoffset: -290;
  animation: visual_svg 0.35s 1.75s linear forwards;
}

#mainVisualWrap .visualTitle svg:last-child {
  margin-left: auto;
  margin-top: 20px;
  stroke-dasharray: 300;
  stroke-dashoffset: 300;
  animation: visual_svg 0.35s 2s linear forwards;
}

@keyframes visual_svg {
  100% {
    stroke-dashoffset: 0;
  }
}

#mainVisualWrap .visualTitle strong {
  font-family: "Plus Jakarta Sans", sans-serif;
  font-size: 120px;
  font-weight: 500;
  line-height: 120px;
  letter-spacing: -0.02em;
  color: #ffffff;
  display: block;
  margin-bottom: 50px;
}

#mainVisualWrap .visualTitle p {
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
  letter-spacing: -0.02em;
  text-align: center;
  color: #f3f3f3;
}

#mainVisualWrap .visualScroll {
  position: absolute;
  bottom: 60px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Plus Jakarta Sans", sans-serif;
  font-size: 13px;
  font-weight: 500;
  line-height: 16.38px;
  letter-spacing: -0.02em;
  text-align: center;
  color: #ffffff;
  opacity: 0;
  transition: 0.75s ease 1.2s;
}

#mainVisualWrap.is_current .visualScroll {
  opacity: 1;
}
#mainVisualWrap .visualScroll::before {
  content: "";
  width: 1px;
  height: 24px;
  display: block;
  background: #ffffff;
  margin-bottom: 5px;
}

#mainContent1 {
  padding: 140px 0 0;
  align-items: flex-start;
}

#mainContent1 hgroup,
#mainContent3 hgroup {
  margin-left: 290px;
}
#mainContent1 hgroup h2,
#mainContent3 hgroup h2 {
  font-size: 38px;
  font-weight: 600;
  line-height: 1.3;
  margin-bottom: 20px;
}

.mainPage hgroup {
