}
.mainPage hgroup p {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.45;
  color: #cfcfcf;
}

.mainPage hgroup a {
  width: fit-content;
  height: 30px;
  font-size: 16px;
  font-weight: 600;
  opacity: 0.5;
  color: #fff;
  border-bottom: 1px solid #fff;
  display: flex;
  align-items: center;
  position: absolute;
  right: 0;
  bottom: 0;
}

.mainPage hgroup a img {
  margin-left: 7px;
}

#mainContent1 .videoWrap {
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: 80px 0 90px;
}
#mainContent1 .partnersWrap {
  width: 100%;
  padding-left: 290px;
}

#mainContent1 .partnersWrap ul {
