#mainContent2 {
  padding: 230px 0 100px;
}

#mainContent2 hgroup {
  padding-left: 290px;
}
#mainContent2 hgroup h2 {
  font-size: 60px;
  font-weight: 600;
  line-height: 1.2;
  margin-bottom: 30px;
}

#mainContent2 .valueWrap {
  width: 100%;
  padding: 0 50px;
  margin-top: 150px;
}

#mainContent2 .valueWrap ul {
  border-top: 1px solid #343434;
}
#mainContent2 .valueWrap ul li {
  border-bottom: 1px solid #343434;
  height: 220px;
  display: flex;
}
#mainContent2 .valueWrap ul li * {
  height: 100%;
}
#mainContent2 .valueWrap ul strong {
  width: calc(100% - 500px);
  border-right: 1px solid #343434;
  font-family: "Plus Jakarta Sans";
  font-size: 200px;
  font-weight: 600;
  line-height: 252px;
  letter-spacing: -0.08em;
  text-align: right;
  color: #303030;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 30px;
}

#mainContent2 .valueWrap ul strong::before {
  content: "";
  border-right: 1px solid #343434;
  width: 240px;
  display: block;
  height: 100%;
  margin-right: auto;
}
#mainContent2 .valueWrap ul strong::after {
