}

#mainContent3 .swiperWrap {
  width: 100%;
  margin-top: 100px;
}

#mainContent3 .swiper {
  width: 100%;
  height: 100%;
}

#mainContent3 .swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background: #222222;
  height: 500px;
  border-radius: 16px;
  box-shadow: 0px 4px 4px 0px #00000040;
  padding: 40px;
  position: relative;
  transition: 0.25s ease;
}

#mainContent3 .swiper-slide:hover {
  background: #3e081f;
}

#mainContent3 .swiper-slide strong {
  font-family: "Plus Jakarta Sans";
  font-size: 24px;
  font-weight: 700;
  line-height: 1.2;
  text-align: left;
  color: #ffffff;
  opacity: 0.14;
  margin-bottom: auto;
}

#mainContent3 .swiper-slide sub {
  font-size: 15px;
  font-weight: 600;
  color: #a82c5e;
}
#mainContent3 .swiper-slide span {
  font-size: 20px;
  font-weight: 600;
  line-height: 1.2;
  margin: 16px 0 10px;
  display: block;
}

#mainContent3 .swiper-slide p {
  font-family: "Plus Jakarta Sans";
  font-size: 15px;
  font-weight: 400;
  line-height: 1.2;
  opacity: 0.4;
  color: #fff;
  text-align: left;
}

#mainContent3 .swiper-slide img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

