#mainContent8 hgroup {
  display: flex;
  align-items: flex-end;
  padding: 0 50px;
  margin-bottom: 50px;
}

#mainContent8 hgroup h2 {
  font-family: "Plus Jakarta Sans";
  font-size: 180px;
  font-weight: 600;
  line-height: 1;
  letter-spacing: -0.04em;
  text-align: right;
  margin-right: 30px;
}

.marqueeWrap {
  position: relative;
  width: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  height: 200px;
  align-items: center;
  border-bottom: 1px solid #212121;
  padding: 0 50px;
}

.marqueeWrap:nth-child(2) {
  border-top: 1px solid #b4b4b4;
}

.marqueeWrap mark {
  font-size: 14px;
  font-weight: 400;
  letter-spacing: -0.02em;
  text-align: center;
  color: #ababab;
  position: absolute;
  right: 20px;
  top: 16px;
}

.marqueeWrap mark strong {
  font-size: 14px;
  font-weight: 600;
  text-align: right;
  color: #fff;
}
.marquee {
  position: absolute;
  display: flex;
  align-items: center;
  white-space: nowrap;
  background: none;
  width: max-content;
  overflow: hidden;
  animation: slidemarquee 50s linear infinite;
  pointer-events: none;
  gap: 40px;
}

.marquee img {
  width: 160px;
  height: 160px;
}

.marqueeWrap.group2 .marquee,
.marqueeWrap.group4 .marquee {
  animation: slidemarquee_reverse 50s linear infinite;
}
.marqueeWrap.group2 .marquee,
.marqueeWrap.group4 .marquee {
  animation: slidemarquee_reverse 50s linear infinite;
}
.marquee2 {
  animation-delay: -25s !important;
  padding: 0 40px;
}

@keyframes slidemarquee {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}
@keyframes slidemarquee_reverse {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

#mainContent9 {
  width: 100%;
  border-radius: 20px 20px 0px 0px;
  background: #18191a;
  padding: 100px 0 80px;
}

#mainContent9 hgroup {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto 60px;
}
#mainContent9 hgroup h2 {
  font-size: 30px;
  font-weight: 500;
  line-height: 1;
  text-align: left;
  margin-top: 10px;
}
#mainContent9 hgroup p {
  color: #c8125b;
}

.inquiryBtn {
  opacity: 1 !important;
}

#mainContent9 div {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 50px 0 100px;
  position: relative;
}

#mainContent9 div strong {
  font-size: 200px;
  font-weight: 600;
  line-height: 1;
  text-align: center;
  color: #2a2c2d;
}

#mainContent9 div img {
  width: 100%;
  max-width: 1660px;
  height: 400px;
  border-radius: 20px;
  opacity: 0.6;
  object-fit: cover;
}

#mainContent9 i,
#mainContent9 div svg {
  position: absolute;
}

#mainContent9 i,
#mainContent9 svg {
  display: block;
}

#mainContent9 i {
  background: #303030;
}

#mainContent9 i.horizontal {
  width: 100%;
  height: 2px;
  transform: scaleX(0);
  transform-origin: left;
}
#mainContent9 i.vertical {
  width: 2px;
  height: calc(100% - 40px);
  transform: scaleY(0);
  transform-origin: top;
}

.contact_line1 {
  top: 0;
}
.contact_line2 {
  top: 20px;
}
.contact_line3 {
  bottom: 20px;
}
.contact_line4 {
  bottom: 0;
}
.contact_line5 {
  top: 20px;
  left: 50px;
}
.contact_line6 {
  top: 20px;
  right: 50px;
}

@keyframes contact_iX {
  100% {
    transform: scaleX(1);
  }
}
@keyframes contact_iY {
  100% {
    transform: scaleY(1);
  }
}

.contact_angle1 {
  stroke-dasharray: 600;
  stroke-dashoffset: -290;
  top: 0;
  left: 50px;
}
.contact_angle2 {
  stroke-dasharray: 600;
  stroke-dashoffset: 600;
  top: 0;
  right: 50px;
}
.contact_angle3 {
  stroke-dasharray: 600;
  stroke-dashoffset: -300;
  bottom: 0;
  left: 50px;
}
.contact_angle4 {
  stroke-dasharray: 600;
  stroke-dashoffset: 600;
  bottom: 0;
  right: 50px;
}

@keyframes contact_svg1 {
  100% {
    stroke-dashoffset: 0;
  }
}
@keyframes contact_svg2 {
  100% {
    stroke-dashoffset: 300;
  }
}
@keyframes contact_svg3 {
  100% {
    stroke-dashoffset: 0;
  }
}
@keyframes contact_svg4 {
  100% {
    stroke-dashoffset: 300;
  }
}

#mainContent9.is_current .contact_line1 {
  animation: contact_iX 2s 0s ease forwards;
}
#mainContent9.is_current .contact_line2 {
  animation: contact_iX 2s 0.25s ease forwards;
}
#mainContent9.is_current .contact_line3 {
  animation: contact_iX 2s 0.5s ease forwards;
}
#mainContent9.is_current .contact_line4 {
  animation: contact_iX 2s 0.75s ease forwards;
}
#mainContent9.is_current .contact_line5 {
  animation: contact_iY 1s 0s ease forwards;
}
#mainContent9.is_current .contact_line6 {
  animation: contact_iY 1s 0.25s ease forwards;
}
#mainContent9.is_current .contact_angle1 {
  animation: contact_svg1 0.35s 0s linear forwards;
}
#mainContent9.is_current .contact_angle2 {
  animation: contact_svg2 0.35s 0.55s linear forwards;
}
#mainContent9.is_current .contact_angle3 {
  animation: contact_svg3 0.35s 0.95s linear forwards;
}
#mainContent9.is_current .contact_angle4 {
  animation: contact_svg4 0.35s 1.25s linear forwards;
}
